import React from 'react';
import Cookie from 'js-cookie'
import cls from './TestFinish.module.css';


const TestFinish = ({ 
    onOnlyFinish, 
    handleFinishAllTest,
    completeQuestions,
    second,
    minute,
    hours,
    QUESTION_SIZE,
     }) => {

    
    
    let time;
    if (Cookie.get("timer")) {
        time = Cookie.get("timer");
    }

    return (
        <div className = {cls.finish}>
            <div className = {cls.finish__inner}>
                <div className = {cls.finish__title}>  ,     ? </div>
                <div className = {cls.finish__result}> : {completeQuestions}/{QUESTION_SIZE} </div>
                <div className = {cls.finish__time}>   : {hours}:{minute}:{second} </div>

                <button className = 'button button__submit mt-10' onClick = { () => handleFinishAllTest(false, time) }> ,   </button>
                <button className = 'button button__over mt-10' onClick = { onOnlyFinish }>  </button>
            </div>
        </div>
    )
}

export default TestFinish;